/* css cho navbar  */
body,
html {
  background-color: #fff;
  font-family: "Poppins", sans-serif;
}

.navbar-nav li .nav-link {
  font-size: 13px;
  padding-top: 17px;
  font-weight: 400;
}

/* .navbar input.form-control {
  border: 2px solid #f3f700 !important;
} */

li.nav-item a {
  transition: 0.2s;
}

li.nav-item:hover a {
  color: #f5a623 !important;
}

a.nav-link {
  padding-top: 8px;
}

a.navbar-brand {
  font-size: 34px;
}

li.nav-item.account {
  margin-right: 0.5rem;
}

a.nav-link {
  margin-left: -0.5rem;
}

.navbar-expand-md .navbar-nav .nav-link.giohang {
  padding-right: 0 !important;
}

i.fa.fa-shopping-cart {
  margin-left: -1px;
  margin-top: 5px;
  display: inline-block;
}

a.btn.btn-secondary.rounded-circle {
  width: 42px;
  height: 40px;
  background-color: #dfdfdf;
  border: #cacaca;
}

.cart-amount {
  height: 18px;
  width: 18px;
  border-radius: 50%;
  background: #f5a623;
  font-size: 12px;
  position: relative;
  left: 19px;
  bottom: 32px;
  transition: 0.2s;
}

.nav-item.giohang:hover .cart-amount {
  background: #f3f700;
  color: white;
}

.dropdown-menu {
  padding: 11px;
  width: 236px;
}

a.dropdown-item {
  background: #f5a623;
  border-radius: 4px;
  font-size: 14px;
  color: white;
  padding: 9px;
}

a.dropdown-item:hover {
  background: #f5a623;
  color: white;
  box-shadow: 0px 2px 5px #f5a623;
}

.navbar-nav .nav-item.active a {
  color: #f5a623;
}

.nutcuonlen {
  visibility: hidden;
  opacity: 0;
  transition: 0.5s;
}

.nutcuonlen.hienthi {
  visibility: visible;
  opacity: 1;
}

.navbar.fixed-top {
  box-shadow: 0px 5px 20px grey;
}
/* het css cho navbar  */

/* css cho header formdangnhap dangky  */
.modal-header {
  padding-bottom: 0;
}

ul.tabs li.tab a {
  font-size: 18px;
  color: #646464;
  transition: 0.2s;
}

ul.tabs li:hover a {
  color: #f5a623;
}

ul.tabs li:hover {
  cursor: pointer;
}

ul.tabs li hr {
  position: relative;
  width: 50%;
  top: 0px;
  left: 25%;
  visibility: hidden;
  border-top: 2px solid #f5a623;
  transition: 0.2s;
}

ul.tabs li:hover hr {
  visibility: visible;
  border-top: 2px solid #f5a623;
  width: 100%;
  left: 0;
}

ul.tabs {
  width: 100%;
  height: 100%;
}

ul.tabs li {
  width: 40%;
  height: 100%;
}

ul.tabs li.active a {
  color: #f5a623;
}
ul.tabs li.active hr {
  visibility: visible;
  border-top: 2px solid #f5a623;
  width: 100%;
  left: 0;
}
/* het css cho header form dangnhap dangky  */

