  border: 1px solid #d7d2d2;
}

.formdanhgia .danhgiacuaban {
  font-size: 14px;
  margin-bottom: 10px;
  display: block;
}

.formdanhgia .tieude {
  font-size: 17px !important;
}

.btn.vietdanhgia {
  border: 1px solid #f7a623;
  color: #f7a623;
  padding: 7px 20px;
}

.btn.vietdanhgia:hover {
  color: white;
  background: #f7a623;
}

.tiledanhgia .motthanh {
  margin-bottom: 8px;
}

#nav-danhgia .tiledanhgia {
  margin-top: 53px;
}

.motthanh i.fa.fa-star {
  margin-left: 6px;
  color: #a5a5a5;
}

.progress {
  width: 80%;
  height: 10px;
}

#nav-danhgia p.sonhanxet {
  font-size: 13px;
}

/* #nav-danhgia .sao i.fa.fa-star {
    color: #d1cccc;
} */

#nav-danhgia .sao .active {
  color: #f5a623;
}

#nav-danhgia .diem {
  font-size: 45px;
  color: red;
  font-weight: bold;
}

.product-description #nav-danhgia p.tieude {
  margin: 20px 0px 0px 0px;
}

.product-description h6.tieude {
  margin-top: 22px;
  font-size: 18px;
}

.product-description .nav-tabs a {
  color: gray;
  margin-right: 7px;
  border-radius: 0;
  font-size: 14px;
}

.product-description .nav-tabs .nav-item.active {
  color: black;
  border-top: 4px solid #f5a623;
}

.formdanhgia .rating input {
  display: none;
}

.formdanhgia .rating label {
  display: block;
  cursor: pointer;
  width: 15px;
}

.formdanhgia .rating label::before {
  content: "\f005";
  font-family: "Font Awesome\ 5 Free";
  font-weight: 900;
  position: relative;
  display: block;
  font-size: 15px;
  color: rgb(187, 186, 186);
}

.formdanhgia .rating label::after {
  content: "\f005";
  font-family: "Font Awesome\ 5 Free";
  font-weight: 900;
  position: absolute;
  display: block;
  font-size: 15px;
  color: #f7a623;
  top: 290px;
  opacity: 0;
  transition: 0.2s;
}

.formdanhgia .rating label:hover::after,
.formdanhgia .rating label:hover~label::after,
.formdanhgia .rating input:checked~label::after {
  opacity: 1;
}

/* het css cho .product-description  */

.khoisanpham .gia .sale {
  background: #f5a623;
  border-radius: 6px;
  font-size: 11px;
  color: white;
  padding: 1px 4px;
  position: absolute;
  font-weight: bold;
  left: 140px;
}

.comment-list {}

.comment-item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 10px 5px;
}

.comment-list .comment-item img {
  width: 50px;
  padding: 5px;
  border-radius: 25px;
  background: #a5a5a5;
}

.comment-item .comment-text {
  word-wrap: break-word;
}

.comment-item .comment-date {
  padding-top: 15px;
}

.size-chart-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.size-chart-content {
  background-color: #fff;
  padding: 5px;
  border-radius: 8px;
  max-width: 90%;
  overflow: auto;
}

.size-chart-content img {
  width: 100%;
  height: auto;
}

.size-chart-content .close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
}

.thumbnail {
  cursor: pointer;
  margin-bottom: 10px;
  width: 80px;
  height: 80px;
  border-radius: 16px;
}

@media screen and (max-width: 768px) {
  .khoianh-container {
    flex-direction: column-reverse;
  }

  .thumbnail {
    width: 50px;
    height: 50px;
  }

  .size-chart-content {
    max-width: 100%;
  }
}